import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {MatSnackBar} from '@angular/material/';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ConfigService} from "../../config-service.service";
import {AppState} from "../../store/State";
import {interval, Observable} from "rxjs/index";
import {Select, Store} from "@ngxs/store";
import {
  GenerateWebSiteAction,
  InitWithDefaultsAction, ThemeChosenAction,
  UpdateEmailAction, WebSiteFieldsUpdatedAction,
  WebSiteGeneratedAction
} from "../../store/actions";
import { switchMap } from 'rxjs/operators';

interface GenerateSiteResponse {
  sitename: string;
}

interface SiteStatusResponse {
  status: string;
}

@Component({
  selector: 'app-inputform',
  templateUrl: './inputform.component.html',
  styleUrls: ['./inputform.component.css']
})
export class InputformComponent implements OnInit {

  isInProgress = false;
  isFinishedUpdatingSite = false;

  @Select() app$;
  emailFromStore: string = null;

  model = new WebSiteProperties('Default Web Site Title', 'The only site description The only site description The only site ' +
    'description The only site description The only site description The only site description The only site description The only ' +
    'site description The only site description The only site description ', 'Top Button Title', 'Want to make this subtitle yourself?',
    'Forget that! Who would ever want to put in all of that effort for a website? Just open up your ' +
    'web browser and type bootstrap template ' +
    'into your favorite search engine, like Yahoo! or Bing, and you\'re on your way! There are hundreds of templates to choose from, but ' +
    'go ahead and pick this same exact template from the first result on google, edit a few lines, ' +
    'and you\'re on your way! No one will notice! :)', 'Section 3 Default Title Internal');
  constructor(private http: HttpClient, public snackBar: MatSnackBar, private formBuilder: FormBuilder,
              private configService: ConfigService, private store: Store) {
    this.store.select(state => {
      this.emailFromStore = state.email;
    })

  }

  ngOnInit() {
    this.store.dispatch([new InitWithDefaultsAction()]);
  }

  onSubmit() {
  }

  checkStatus(sitename: string): void {
    this.http.get<SiteStatusResponse>(`${this.configService.config.serverURL}/justsaywebsite/status/?sitename=${sitename}`).subscribe(data => {
      console.log(`status: ${JSON.stringify(data)}`);

      const that = this;

      if (data.status !== 'done') {
        console.log(`status is not done yet its: ${data.status}`);
        setTimeout(function() { that.checkStatus(sitename); }, 5000)
      } else {
        this.store.dispatch([new WebSiteGeneratedAction(`https://${sitename}.netlify.com`)]);
        this.isInProgress = false;
        this.isFinishedUpdatingSite = true;
        this.openSnackBar('Your website is ready', 'click above link to open it!');
      }
    }, error => {
      console.log(`an error has occured: ${error.message}`);
      this.isInProgress = false;
    });
  }


  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 10000,
    });
  }

  genereateWebSite(themeConfig: any) {
    this.store.dispatch([new GenerateWebSiteAction(themeConfig)]);

    console.log('form submitted...');
    this.isInProgress = true;
    console.log('model: ' + JSON.stringify(this.model));

    this.store.selectSnapshot(state => {

      console.log(`onSubmit state: ${themeConfig}`);

      this.http.post<GenerateSiteResponse>(
        this.configService.config.serverURL,
        { email: state.app.email, pageData: JSON.stringify(themeConfig)}).subscribe(data => {

        this.checkStatus(data.sitename);
        // this.store.dispatch([new WebSiteGeneratedAction(data.sitename)]);
        // this.isInProgress = false;
        // this.isFinishedUpdatingSite = true;
        // this.openSnackBar('Your website is ready', 'click above link to open it!');
      }, error => {
        console.log(`an error has occured: ${error.message}`);
        this.isInProgress = false;
      });

    });


  }



  getTitle(): Observable<string> {
    return this.app$.map(state => {
        return state.everyBootstrapThemeConfig.match('title: &quot;(.*)&quot;')[1];
      }
    );
  }

  onFieldsChange(title: string, subtitle: string, description: string, topButtonTitle: string, subDescription: string) {
    this.store.dispatch([new WebSiteFieldsUpdatedAction(title, subtitle, description, topButtonTitle, subDescription)]);
  }

  onThemeChange(theme: string) { this.store.dispatch([new ThemeChosenAction(theme)]); }

}

export class WebSiteProperties {
  constructor (public title: string, public description: string,
               public topButtonScrollTitle: string, public subtitle: string,
               public subdescription: string, public section3Title: string) {}
}
