import { Component, OnInit } from '@angular/core';
import {AuthService, GoogleLoginProvider, SocialUser} from 'angular-6-social-login';
import {ConfigService} from "../../config-service.service";
import {Select, Store} from "@ngxs/store";
import {UpdateEmailAction} from "../../store/actions";

@Component({
  selector: 'app-sociallogin',
  templateUrl: './sociallogin.component.html',
  styleUrls: ['./sociallogin.component.css']
})
export class SocialLoginComponent {

  user: SocialUser;

  @Select() app$;

  constructor(private socialAuthService: AuthService, private configService: ConfigService,
              private store: Store) { }

  public socialSignIn(socialPlatform : string) {
    let socialPlatformProvider;
      socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;

    this.socialAuthService.signIn(socialPlatformProvider).then(
      (userData) => {
        this.user = userData;
        console.log(socialPlatform+" sign in data : " , userData);
        this.store.dispatch([new UpdateEmailAction(userData.email, userData.name, userData.image)])
      }
    );

  }

  signOut(): void {
    this.user = null;
    this.socialAuthService.signOut();
    this.store.dispatch([new UpdateEmailAction(null, null, null)])
  }



}
