export interface AppStateModel {
  username: string;
  email: string;
  photoURL: string;
  webSiteUrl: string;
  everyBootstrapThemeConfig: string;
}

@State<AppStateModel>({
  name: 'app',
  defaults: {
    username: null,
    email: null,
    photoURL: null,
    webSiteUrl: null,
    everyBootstrapThemeConfig: everyBootstrapThemeConfigDefault
  }
})

export class AppState {

  @Selector()
  static getUser(state: AppStateModel) {
    return state.email;
  }

  @Action(UpdateEmailAction)
  setEmail({getState, patchState }: StateContext<AppStateModel>, { email, username, photoURL }: UpdateEmailAction) {
    patchState({ email: email, username: username, photoURL: photoURL });
  }

  @Action(WebSiteGeneratedAction)
  setWebSiteUrl({getState, patchState }: StateContext<AppStateModel>, { webSiteUrl }: WebSiteGeneratedAction) {
    patchState({ webSiteUrl: webSiteUrl });
  }

  @Action(GenerateWebSiteAction)
  setThemeConfig({getState, patchState }: StateContext<AppStateModel>, { everyBootstrapThemeConfig }: GenerateWebSiteAction) {
    patchState({ everyBootstrapThemeConfig: everyBootstrapThemeConfig });
  }

  @Action(ResetWebSiteFieldsAction)
  resetWebSitefields({getState, patchState }: StateContext<AppStateModel>, { }: GenerateWebSiteAction) {
    patchState({ everyBootstrapThemeConfig: everyBootstrapThemeConfigDefault });
  }

  @Action(WebSiteFieldsUpdatedAction)
  updateStateByFields({getState, patchState }: StateContext<AppStateModel>, { title, subtitle, description, buttonTitle, midPageDescription }: WebSiteFieldsUpdatedAction) {

    const currentThemeConfig = getState().everyBootstrapThemeConfig;

    const updatedThemeConfig =
      currentThemeConfig.replace(/^title: "(.*?)$/gm, `title: "${title}"`)
        .replace(/^top_left_title: "(.*?)$/gm, `top_left_title: "${subtitle}"`)
        .replace(/^moredetails_section_description: "(.*?)$/gm, `moredetails_section_description: "${description}"`)
        .replace(/^top_button_page_scroll_title: "(.*?)$/gm, `top_button_page_scroll_title: "${buttonTitle}"`)
        .replace(/^multicolumn_section_first_column_description: "(.*?)$/gm, `multicolumn_section_first_column_description: "${midPageDescription}"`);

    patchState( { everyBootstrapThemeConfig: updatedThemeConfig } );

  }

  @Action(ThemeChosenAction)
  themeChosen( { getState, patchState }: StateContext<AppStateModel>, { theme }: ThemeChosenAction) {
    const currentThemeConfig = getState().everyBootstrapThemeConfig;

    const updatedThemeConfig =
      currentThemeConfig.replace(/^justsaywebsite_theme: "(.*?)$/gm, `justsaywebsite_theme: "${theme}"`);

    patchState( { everyBootstrapThemeConfig: updatedThemeConfig } );
  }


  @Action(InitWithDefaultsAction)
  initState( { getState, patchState }: StateContext<AppStateModel>, { }: InitWithDefaultsAction) {
â€‹
    if (getState().everyBootstrapThemeConfig == null) {
      patchState({ everyBootstrapThemeConfig: everyBootstrapThemeConfigDefault });
    }

    if (getState().everyBootstrapThemeConfig.indexOf('justsaywebsite_theme') == -1) {
      patchState({ everyBootstrapThemeConfig: everyBootstrapThemeConfigDefault + '\njustsaywebsite_theme: jekyll-theme-03-every-boostrap'});
    }
  }


}
